import React from 'react'
import ReactDOM from 'react-dom'

import Grid from './Grid'
import LinkFormatter from './Grid/formatters/Link'

const data = [
  {
    id: 'id1',
    name: {
      label: '',
      href: 'https://abroller.tech/'
    },
    sex: '',
    birth: '2011/11/09'
  },
  {
    id: 'id2',
    name: {
      label: '',
      href: 'https://abroller.tech/'
    },
    sex: '',
    birth: '1988/12/06'
  },
  {
    id: 'id3',
    name: {
      label: '',
      href: 'https://abroller.tech/'
    },
    sex: '',
    birth: '2012/11/09'
  },
  {
    id: 'id4',
    name: {
      label: '',
      href: 'https://abroller.tech/'
    },
    sex: '',
    birth: '1995/11/09'
  },
];

const columns = [
  { key: "name", label: "", formatter: LinkFormatter },
  { key: "sex", label: "", options: ["", ""] },
  { key: "birth", label: "" },
]

ReactDOM.render(
  <>
    <section>
      <h1 className="heading-lv2"></h1>
      <dl>
        <dt></dt>
        <dd></dd>
        <dt>Home / End</dt>
        <dd></dd>
        <dt>Control + Home / Control + Home</dt>
        <dd>(:0, 0)</dd>
        <dt>Shift + Space</dt>
        <dd>/</dd>
        <dt>Control + A</dt>
        <dd></dd>
      </dl>
    </section>
    <section>
      <h1 id="heading"></h1>
      <Grid ariaLabelledby="heading" data={data} columns={columns} />
      <button></button>
    </section>
  </>,
  document.getElementById('app')
);


